import React , {Component} from 'react'
import {Container, Row , Col, Button} from "react-bootstrap";

class Modal extends Component{


render() {

return (


<div>


 {this.props.project.map(pro => {


 if(this.props.projectid === pro.id) {

 return (<div id="myModal"  className="modal">
  <span class="close" onClick={this.props.close}>&times;</span>

<Container className="modal-box">

<Row >

<Col  xl={6} md={6} sm={6} xs={6} className="cols">
 <a href="#section3"> <iframe src={pro.project_gif} title={pro.project_name} frameBorder="0"></iframe> </a>
 </Col>



 <Col  xl={6} md={6} sm={6} xs={6} className="cols-2">

<h3> {pro.project_name}</h3>
<hr className="head-line"/>

<p className="pro-des">   {pro.des}</p>

<a href={pro.demo_url}  target="__blank" > <Button  variant="primary" className="box-button">Preview Site ! </Button> </a>

<a href={pro.code_url}  target="__blank" > <Button variant="danger" className="box-button"> See Code ! </Button> </a>
  </Col>

</Row>


</Container>


</div> )
}

}
)
}


</div>
)



}


}


export default Modal;
