import React from 'react'
import {Container, Row , Col} from "react-bootstrap";

const Section2 = (props) => {
  return (
<Container fluid={true}>

    <Row style={{marginTop:'300px'}}>

    <Col xl={12} className="section-features">

<div className="box3" >

   <div className ="inner-section">


     <span className="awesome">  <i class="fa fa-code"/></span>

     <h6>FRONT END</h6>
     <p>HTML5</p>
      <p>CSS3|SAAS</p>
       <p>JAVASCRIPT</p>
        <p>REACT | REDUX</p>

</div>

   <div className ="inner-section">

<span className="awesome">  <i class="fa fa-database"/></span>
  <h6>DATABASE</h6>
  <p>PostgreSQL </p>
 <p>mySQL</p>
<p>MongoDB</p>
 <p>Cassandra</p>
</div>

   <div className ="inner-section">


     <span className="awesome">  <i class="fa fa-share-alt"/></span>
       <h6>SERVER</h6>
       <p>NodeJS </p>
       <p>Express</p>
       <p> Koa</p>
       <p>Python</p>


</div>

   <div className ="inner-section">


     <span className="awesome">  <i class="fa fa-cloud"/></span>
   <h6>DEPOLYMENT</h6>
  <p> System Design</p>
  <p>  AWS ECS | SQS</p>
  <p>    Docker  </p>
  <p>   Git </p>


</div>


</div>

       </Col>
       </Row>

       </Container>
  )
}

export default Section2;
