
import React from 'react'
import {Spring} from 'react-spring/renderprops';
import  { Row, Col , Button} from 'react-bootstrap';
import Logo from "../assets/logo.png";

class Header extends React.Component {

  render() {
  return (

      <Row>
      <Col  xl={12} md={12} sm={12} xs={12} className="box">


        <div id="section1">
  <a href="#default" id="logo"><img src={Logo}  alt="logo" width="50px" height="40px" /></a>

    <Spring
      from={{ opacity: 0 , marginLeft: -300 }}
      to={{ opacity: 1 , marginLeft: 0  }}
      config={{delay:400 , duration: 1000}}>
      {props =>   <h1 style={props}>DANISH KHAN </h1> }
    </Spring>



    <Spring
      from={{ opacity: 0 , marginRight: -200}}
      to={{ opacity: 1 , marginRight: 0  }}
      config={{delay:500 , duration: 1000}}>
      {props =>  <div style={props}>  <p className="front-end">FRONT END ENGINEER</p> </div> }
    </Spring>



    <Spring
      from={{ opacity: 0 , marginTop: -40}}
      to={{ opacity: 1 , marginTop: 0  }}
      config={{duration: 1100}}>
      {props =>  <div style={{marginTop:40}}><a  style={props} href="#section3"> <Button style={props} className="btns"> SEE PROJECTS  </Button> </a> </div> }
    </Spring>

    <div className="social">

    <a href="https://www.linkedin.com/in/danish-khan-5a95b6174/">

    <i className="fa fa-linkedin-square"/>

    </a>




    <a href="https://github.com/danish0313">

    <i className="fa fa-github-square"/>

    </a>


    <a href="https://www.xing.com/profile/Danish_Khan25/cv?sc_o=mxb_p">

    <i className="fa fa-xing-square"/>

    </a>

    </div>



  </div>


</Col>
</Row>

    );
  }
}


export default Header;
