import React, {Component} from 'react'
import {Container, Row , Col, Button} from "react-bootstrap";
import axios from 'axios';

import Modal from '../modal/modal';
class Section3 extends Component {

state = {

projects : [],
show : false,
project_id : ''
}


componentDidMount() {

axios.get('https://portfolio-e7a09.firebaseio.com/projects.json')
.then(res => {


  let data = [];
  for (let key in res.data) {

      data.push({
     ...res.data[key],
     id :  key

      }) };

this.setState({

projects : data


})

console.log(this.state.projects )


})


}


// showng Modal

modalhandler = (id) => {

  this.setState({

show : true,

project_id : id
  })




}

close = () => {


this.setState(prevState => {

return {


  show : !prevState.show
}


})


}


  render() {
  return (
<React.Fragment>



        <Row id="section3">

        <Col xl={12} md={12} sm={12} xs={12} style={{marginTop: '50px'}} className="box">
<h2>    PROJECT PORTFOLIO</h2>

      </Col>
</Row>



<Container>

<Row style={{marginTop: '50px'}}>



{this.state.projects.map((pro) =>

  (
    <div className="pro" key={pro.id}>

      <div className="flip">

    <div class="flip-card" >
          <div class="flip-card-inner"  >
            <div class="flip-card-front">

      <div className="upper">
      <iframe src={pro.gif} title='burger' frameBorder="0" className="giphy-embed" allowFullScreen ></iframe>
         <h5>{pro.project_name}</h5>
      </div>

      <div className='flip-body'>

        <img src={pro.image} alt={pro.project_name} className="gear-spin"/>

<div className="hidden">

<a href={pro.demo_url}  target="_blank"  rel="noopener noreferrer" > <Button   className="mobile">Preview Site ! </Button> </a>
&nbsp;&nbsp;



<a href={pro.code_url}  target="_blank"  rel="noopener noreferrer"> <Button  className="mobile"> See Code ! </Button> </a>

</div>
      <div className="frameworks">
        <p>{pro.skills.html}</p>
        <hr className="underline"/>
         <p>{pro.skills.css}</p>

      <hr className="underline"/>
          <p>{pro.skills.javascript}</p>
            <hr className="underline"/>
           <p>{pro.skills.redux}</p>
<hr className="underline"/>
      </div>
            </div>


          </div>


            <div class="flip-card-back" style={{backgroundColor: pro.back_color}}>
              <h5>{pro.project_name}</h5>
              <p>{pro.des}</p>

        <a href="#section3"> <Button variant="success"  onClick={() => this.modalhandler(pro.id)} className="btns"> SEE DEMO ! </Button> </a>


            </div>
          </div>
        </div>
        </div>

       {this.state.show === true ?  <Modal close={this.close} projectid = {this.state.project_id} project={this.state.projects}/> : null  }
  </div>



  ))}



  </Row>
  </Container>
</React.Fragment>



  )
}
}

export default Section3;
