import React from 'react'
import {Row , Col} from "react-bootstrap";
import Java from "../assets/java.png";
import Node from "../assets/node.png";
import Rent from "../assets/react.png";
const Body = (props) => {
  return (

<React.Fragment>
<Row>

<Col xl={12} md={12} sm={12} xs={12} className="box">

<h2>       FRONT END FOCUSED <br/> WITH FULL STACK EXPERIENCE</h2>


   </Col>

</Row>



<Row>

<Col xl={6} md={6} sm={12} xs={12} className="box2">
<div className="inner-box">
<h5>MOTIVATED TO PRODUCE RESULTS</h5>
<p>As a tenacious self-taught programmer, I use continuous iteration to produce results quickly and continuously improve products.
</p>
<h5>AN AGILE COLLABORATOR  </h5>
<p>I have contributed to open source projects, worked on engineering teams, and always stay attuned to the newest frameworks.
</p>

</div>

   </Col>

   <Col xl={6} md={6} sm={12} xs={12} className="box2">

<img src={Java} width="150px" className="composer" alt="JavaScript"/>
<img src={Node} width="150px" className="composer" alt="nodejs"/>
<img src={Rent} width="150px" className="composer" alt="react.js"/>
</Col>

</Row>


</React.Fragment>


  )
}

export default Body
